import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import logo from '../images/R-logo.png'
import './header.scss'
import './layout.scss'

class Header extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      hasScrolled: false
    }
  }

  componentDidMount() {
    window.addEventListener('scroll',this.handleScroll);
  }

  handleScroll = (event) =>{
    const scrollTop = window.pageYOffset;

    if(scrollTop > 1000) {
      this.setState({ hasScrolled:true });
    } else {
      this.setState({ hasScrolled:false });
    }
  }

  render(){
    return(
      <div className={this.state.hasScrolled ? "header headerScrolled" : "header"}>
        <div className="nav">
          <div className="nav__left">
              <Link to="/">
                <img className="nav__left-logo" src={logo} width="70px" alt="Logo"/>
              </Link>
              <Link className="nav__left-title" to="/"> <span>Tausif</span>Patel</Link>
          </div>
          <div className="nav__right">
            <Link className="nav__link" to="#projects">Projects</Link>
            <Link className="nav__link" to="photography">Photography</Link>
            <Link className="nav__link" to="me">Me</Link>
          </div>
        </div>
      </div>
    )
  }
}


Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
